


.search {
    width: 100%;
    background-color: rgba(248, 213, 213, .8);
    padding: 10px 0;
    border-bottom: 1.5px rgb(116, 0, 0) solid;
    align-content: center;
    justify-content: center;
    display: flex;
}
.search_bar {
    width: 60%;
    display: flex;
}
.search_bar > input {
    display: inline-block;
    width: 100%;
    padding: 7.5px;
    border-radius: 15px;
    font-weight: 500;
}
.search_bar > input::placeholder {
    font-weight: 300;
}
.search_icon {
    display: inline-block;
    border: 1.5px solid rgb(0, 0, 0);
    background-color: white;
    color: rgb(255, 40, 40);
    width: 40px;
    height: 40px;
    padding: 5px;
    margin: auto 5px;
    border-radius: 100%;
    position: relative;
    transition: all .2s ease-in;
}
.search_icon:hover {
    cursor: pointer;
}


.search_icon svg {
    /* display: inline-block; */
    vertical-align: middle;
    /* transform: scale(1.5); */

    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    height: 20px;
    
    position: absolute;
    transition: all .1s ease-out;
}
.search_icon:hover {
    background-color: red;
    color: white
}

.search_entry {
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    color: rgb(255, 0, 0);
    border-radius: 50px;
    padding: 7.5px 12.5px 7.5px;
    font-weight: 700;
    font-size: larger;
    min-width: fit-content;
    border: 1.5px solid rgb(0, 0, 0);

    
    transition: all 0.2s ease-in-out;
}

.search_entry:hover {
    background-color: red;
    color: white;
    cursor: pointer;
}