.login {
    background-color: rgb(252, 228, 228);
    border-radius: 6px;

    width: 350px;
    height: fit-content;
    padding-bottom: 2.5%;
    padding-top: 1%;
    margin: 5% auto;

    /* position: absolute; */

    /* justify-self: right; */
    /* margin: 10%; */
}

.login__signIn {
    position: relative;
    top: 5px;
    font-size: 20px;
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    font-size: 22.5px;
}

.login__password,
.login__username {
    border-radius: 6px;
    margin: 20px;
    display: flex;
    flex-direction: column;
    text-align: left;
}

.login__pass-input,
.login__pass-title {
    margin-top: 0;
    margin-bottom: 0;
    border-radius: 6px;
    height: 25px;
}

.login__user-input,
.login__user-title {
    margin-top: 0;
    margin-bottom: 0;
    border-radius: 6px;
    height: 25px;
}

.login__pass-title,
.login__user-title {
    font-weight: 700;
}

.login__login {
    display: flex;
    flex-direction: column;
    margin: 0 20px 5px 20px;
}
.login__login-button {
    padding: 7.5px 0;
    background-color: rgb(255, 44, 79);
    border-radius: 6px;
    border: 1px solid red;
    font-weight: 700;
    color: white;
}
.login__login-button:nth-child(2) {
    margin-top: 10px;
}
.login__login-button:hover {
    background-color: #ffd3da;
    box-sizing: content-box;
    border: 1px solid red;
    color: rgb(255, 0, 0);
    cursor: pointer;
}

.login__register {
    font-size: 12px;
    margin-bottom: 10px;
}
.inline-sign {
    color: red;
    font-weight: 700;
}

a {
    z-index: 2;
    width: 300px;
    height: 300px;
    position: absolute;
}

.circle-container {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    width: 200px;
    height: 200px;
    margin: 0 auto;
    z-index: -1;
}

.circle {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    color: #ad3737fd;
    transform: translate(-50%, -50%);
    animation: orbit 20s infinite linear;
}

.circle:nth-child(1) {
    animation-delay: 0s;
}

.circle:nth-child(2) {
    animation-delay: -2s;
}

.circle:nth-child(3) {
    animation-delay: -4s;
}

.circle:nth-child(4) {
    animation-delay: -8s;
}

.circle:nth-child(5) {
    animation-delay: -10s;
}

.circle:nth-child(6) {
    animation-delay: -12s;
}

.circle:nth-child(7) {
    animation-delay: -14s;
}

.circle:nth-child(8) {
    animation-delay: -16s;
}

.circle:nth-child(9) {
    animation-delay: -18s;
}

.circle:nth-child(10) {
    animation-delay: -20s;
}

@keyframes orbit {
    0% {
        opacity: 1.0;
        transform: translate(-50%, -50%) rotate(0deg) translate(5px);
    }

    90% {
        opacity: 0.9;
    }

    100% {
        opacity: 0;
        transform: translate(-50%, -50%) rotate(1550deg) translate(-450px);
    }
}

@media (max-width: 700px) {
    .login {
      width: 80%;
      margin: 10% auto;
    }
    .circle-container {
      display: none;
    }
  }
  