.background__layer1 {
    background-color: rgb(255, 166, 166);
    width: 1300px;
    height: 1300px;

    transform: translate(-20%, -40%);
    position: fixed;
    top: 50%;
    left: 10%;
    border-radius: 100%;
    z-index: -2;
    box-shadow: 2px 2px 7px 1px rgba(0, 0, 0, 0.3);
}
.background__layer2 {
    background-color: rgb(252, 139, 139);
