

.ratinglist_ratings {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 auto;
    width: 70%;
}

.ratinglist-button {
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    color: rgb(255, 0, 0);
    border-radius: 50px;
    padding: 10px 12.5px 10px;
    margin: 10px 0 50px 0;
    font-weight: 700;
    font-size: larger;
    border: 1.5px solid rgb(0, 0, 0);

    
    transition: all 0.2s ease-in-out;
}

.ratinglist-button:hover {
    background-color: rgb(255, 0, 0);
    color: white;
    cursor: pointer;
}