/* * { border: 3px solid black; } */


.rating {
    display: flex;
    flex-direction: column;
    background-color: whitesmoke;
    border-radius: 10px;
    width: 85%;
    min-width: 250px;
    min-width: none;
    margin: 10px; 
    border: 3px solid rgb(0, 0, 0);

    transition: transform 0.2s ease-in-out;

}
.rating:hover {
    transform: scale(1.05);
    cursor: pointer;
}



.rating_header {
    display: flex;
}
.rating_rated {
    text-align: left;
    flex-basis: 50%;
    margin: 15px 0 0 10px;
}
.rating_rated_name {
    font-size: 1.5em;
    display: flex;
    font-weight: bold;
}
.rating_rated_location {
    font-size: .75em;
    /* vertical-align: sub; */
}

.rating_rater {
    display: flex;
    flex-direction: column;
    text-align: right;
    flex-basis: 50%;
    color: gray;
    margin: 15px 10px 0px 0px;
    row-gap: 7.5px;
}
.rating_rater_name {
    font-weight: 400;
    font-size: x-small;
}
.rating_rater_score {
    font-size: 1.5em;
    font-weight: 300;
    font-family: 'Anton', sans-serif;
    color: rgb(255, 0, 0);
}
.rating_rater_score-total {
    font-size: x-small;
    font-weight: 700;
    
    color: gray;

}


hr {
    width: 95%;
}
.rating_description {
    margin: 5px 15px 10px;
    text-align: left;
    font-weight: 400;
}

